$primary-color: #091646;
$secondary-color: #857bef;
$font-stack:georgia;

body,
h1,
h2,
p{
  margin: 0; padding: 0; box-sizing: border-box;
}

body {
  font-family: $font-stack; line-height: 1.6;
  background-color: #8bcae8;
}

header{
  text-align: center; padding: 24px; color: #fff;
  background-color: $primary-color;
}

main{
  padding: 24px; margin-top: 24px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif
}
.name{
  color:#263660;
  font-family: "Garamond";
}

footer{
  text-align: center; padding: 8px; margin-top: 24px; color: #fff;
  background-color: $primary-color;
}

h2 {
  color: #333; margin-bottom: 8px;
}

nav {
  margin: 24px 0;
  ul{ list-style-type: none; margin: 0; padding: 0; overflow: hidden;
  background-color: $primary-color;
  }
  li{
    float: left;
    a{
      display: block; color: white; text-align: center; padding: 8px 16px;
      text-decoration: none; 
      &:hover:not(.active) {
        background-color: $secondary-color;
      }
    }
  }
}

.container {
  width: 80%; margin: auto; overflow: hidden;
}

.profile-picture {
  width: 150px; height: 150px; border-radius: 50%; margin: 24px auto;
  background-color: $secondary-color;
}

.active {
  background-color: $secondary-color;
}

/* Friends Page */
.grid-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  padding: 20px;
}

.friend-card {
  background: #cce5ff; /* Warna biru lembut */
  color: #000;
  padding: 20px;
  border-radius: 20%; /* BUAT BULAT */
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 8px 16px rgba(0,0,0,0.2);
  transition: transform 0.3s ease;
  text-align: center;
  font-size: 14px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.friend-card:hover {
  transform: scale(1.1); /* Efek zoom pas hover */
  background: #99ccff;
}

.friend-card a {
  text-decoration: none;
  color: inherit;
  display: block;
}


/* Blog Page */
.blog-container {
    width: 80%;
    margin: auto;
    text-align: center;
}

.blog-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Buat kolom otomatis */
    gap: 20px;
    margin-top: 20px;
}

.blog-card {
    background-color: #adbaf3;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    transition: 0.3s;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
}

.blog-card:hover {
    background-color: #857bef;
    border-color: #95e4f7;
}

.blog-card h2 a {
    text-decoration: none;
    color: #58aed1; 
}

.blog-date {
    color: #181717;
    font-size: 14px;
}

.blog-excerpt {
    font-size: 16px;
    color: #413f3f;
}


.bubble-effect {
  background-color: #111;
  color: white;
  padding: 1rem;
  display: inline-block;
  border-radius: 20px;
  box-shadow: 0 0 10px 5px rgba(0,255,255,0.8);
  transition: all 0.3s ease;
}
.bubble-effect:hover {
  transform: scale(1.1);
  box-shadow: 0 0 20px 10px rgba(255, 255, 0, 0.8);
}

.bubble-effect:hover {
  box-shadow: inset 1px 1px 4px rgba(93, 214, 77, 0.1),
              inset -1px -1px 4px rgba(138, 132, 228, 0.6);
  transform: scale(1.02);
    color : blue;
}
last_name {
    color : greenyellow;
}